.banner{
  width: 100%;
}

.nav{
  width: 100%;
  height: 100px;
  z-index: 50;
  position: fixed;
  top: 0px;
  background: rgba(0, 0, 0, 0.71);
  padding: 15px 55px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.info{
  width: 100%;
  height: 1000px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.flechasTitle{
  margin: 20px 8px;
  box-sizing: border-box;
}

.button{
  background-color: #BE8E2B;
  padding: 18px 27px;
  font-family: Raleway;
  font-size: 16px;
  color: white;
  margin-top: 45px;
  border-radius: 10px;
  border-width: 0px;
}

.charactersList{
  padding-top: 150px;
}

.componentsDiv{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 0px 35px;
  box-sizing: border-box;
  /*margin-top: 100px;*/ 
}

.titlesAllComp{
  font-family: Volkhov;
  font-weight: 400;
}

.titlesSections{
    font-family: Anton;
    font-size: 35px;
    font-weight: 400;
}

.normalTitles{
  margin-bottom: 90px;
}

.componentsButton{
  background-color: #BE8E2B;
  width: 80%;
  padding: 10px 0px;
  font-family: Raleway;
  font-size: 16px;
  color: white;
  margin-top: 10px;
  border-radius: 10px;
  border-width: 0px;
}

.button:hover{
  background-color: #F4A500;
}
.componentsButton:hover{
  background-color: #F4A500;
}

.deleteButton{
  background-color: #A03333;
}

.deleteButton:hover{
  background-color: #a31f1f;
}

.formTitle{
  width: 100%;
  margin-top: 150px;
  padding-left: 40px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.formTypes{
  margin-top: 50px;
  width: auto;

}
.Link{
    font-family: Raleway;
    color: white;
    box-sizing: border-box;
    text-decoration: none;
    font-size: 17px;
    margin-right: 25px;
}

.Link:hover{
    font-weight:bolder;
    color: white;
}

.Link--active{
    font-weight:bolder;
    color: white;
}

.MainTitle{
    width: 59%;
    display: flex;
    align-self: center;
    justify-self: center;
    margin-top: 200px;
    margin-bottom: 150px;
}

.titleMain{
    font-family: Anton;
    font-size: 35px;
    font-weight: 400;
}
.ElementTitle{
    width: 100%;
    display: flex;
    align-items: center;
    padding-left: 120px;
    box-sizing: border-box;
    margin-bottom: 90px;
}

.titleElement{
    margin-left: 39.5%;
}


.character{
width: 27%;
height: 500px;
display: flex;
flex-direction: column;
/*background-color: blue;*/
}

.characterName{
    width: 100%;
    margin: 0px;
    font-size: 35px;
    display: flex;
    justify-content: right;    
}

.characterForm{
    width: 100%;
    padding-top: 60px;
    padding-left: 40px;
    box-sizing: border-box;
}

.characterForm label{
    width: 35%;
    font-size: 18px;
    font-family: Raleway;
    display: block;
    margin-top: 25px;
}

.characterForm input {
    width: 100%;
    display: block;
    background: rgba(255, 255, 255, 0.164);
    padding: 10px 10px;
    border-radius: 5px;
    border-color: white;
    color: white;
    margin-top: 5px;
    font-size: 16px;
    font-family: Raleway;
}

.characterForm select{
    width: 105%;
    display: block;
    background: rgba(255, 255, 255, 0.164);
    padding: 10px 10px;
    border-radius: 5px;
    border-color: white;
    color: rgb(160, 160, 160);
    margin-top: 5px;
    font-size: 16px;
    font-family: Raleway;
}

.titleForm{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    font-family: Anton;
    font-weight: 400;
}

.subtitleForm{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-top: 35px;
}

.largeInput{
    width: 100%;
    display: block;
    background: rgba(255, 255, 255, 0.164);
    padding: 10px 10px;
    height: 70px;
    border-radius: 5px;
    border-color: white;
    color: white;
    font-size: 16px;
    font-family: Raleway;
}

.CharacterForm_error{
    color: rgb(238, 22, 22);
    font-size: 16px;
}
.weapon{
    width: 20%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.weaponImg{
    width: 90%;
    margin-top: 10px;
}
.artifacts{
    width: 20%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.artifactName{


}

.artifactImg{
    width: 80%;
}

